import { templates } from "./templates.js";
import { renderFormFields } from "./form.js";
import { updatePreview } from "./preview.js";
import { pasteToOutlook } from "./outlook.js";

export let selectedImage = null;

// This function will initialize the add-in's main logic.
function initializeAddIn() {
  const templateSelect = document.getElementById('templateSelect');
  document.getElementById("sideload-msg").style.display = "none";

  Object.keys(templates).forEach(key => {
    const option = document.createElement('option');
    option.value = key;
    option.textContent = templates[key].name;
    templateSelect.appendChild(option);
  });

  renderFormFields(templateSelect.value);
  updatePreview();

  const imageUploadContainer = document.getElementById('imageUploadContainer');
  const imageUpload = document.getElementById('imageUpload');

  const updateVisibility = () => {
    if (templateSelect.value === 'keyFob') {
      imageUploadContainer.style.display = 'block';
    } else {
      imageUploadContainer.style.display = 'none';
    }
  };

  templateSelect.addEventListener('change', () => {
    renderFormFields(templateSelect.value);
    updatePreview();
    updateVisibility();
  });

  updateVisibility();

  imageUpload.addEventListener('change', (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const img = new Image();
        img.onload = () => {
          if (typeof cv === 'undefined') {
            console.error('OpenCV.js is not loaded.');
            return;
          }

          const mat = cv.imread(img);
          const dst = new cv.Mat();
          const dsize = new cv.Size(0, 0);
          const maxDim = 600;
          let width = mat.cols;
          let height = mat.rows;

          if (width > height) {
            height = Math.round(height * maxDim / width);
            width = maxDim;
          } else {
            width = Math.round(width * maxDim / height);
            height = maxDim;
          }

          cv.resize(mat, dst, dsize, width / mat.cols, height / mat.rows, cv.INTER_AREA);
          cv.imshow('imageCanvas', dst);

          const canvas = document.getElementById('imageCanvas');
          selectedImage = canvas.toDataURL('image/jpeg', 0.8);

          mat.delete();
          dst.delete();

          updatePreview();
        };
        img.src = e.target.result;
      };
      reader.readAsDataURL(file);
    }
  });

  document.getElementById('pasteButton').addEventListener('click', async () => {
    await pasteToOutlook(templateSelect.value, selectedImage);
  });
}

// Ensure Office.js is ready before anything else runs.
Office.onReady(() => {
    // Then, wait for the window to finish loading, which includes OpenCV.js.
    window.onload = () => {
        initializeAddIn();
    };
});