// graph.js

// Import the Microsoft Graph JS SDK
import { Client } from "@microsoft/microsoft-graph-client";

// Define the scopes required for Microsoft Graph API calls.
// This must match the scopes declared in your add-in's manifest.xml.
const graphScopes = ["Files.ReadWrite.All"];

/**
 * Creates a custom authentication provider for the Microsoft Graph client
 * using Office.js authentication.
 */
class OfficeAuthClientAuthenticationProvider {
  /**
   * This method is called by the Graph client to get an access token.
   * It uses the modern, promise-based getAccessToken method.
   * @returns {Promise<string>} A promise that resolves with the access token.
   */
  async getAccessToken() {
    try {
      const token = await Office.auth.getAccessToken({
        allowSignInPrompt: true,
        allowConsentPrompt: true,
        forMSGraphAccess: true,
        scopes: graphScopes,
      });
      console.log("[GRAPH API] Access token acquired successfully.");
      return token;
    } catch (error) {
      console.error("[GRAPH API] Failed to acquire access token:", error.message);
      // It's important to throw the error to halt the graph API call
      throw error;
    }
  }
}

/**
 * Creates and initializes a Microsoft Graph client instance.
 * @returns {Promise<Client>} A promise that resolves to the initialized Microsoft Graph client.
 */
async function createGraphClient() {
  const authProvider = new OfficeAuthClientAuthenticationProvider();
  const client = Client.initWithMiddleware({ authProvider });
  return client;
}

/**
 * Uploads a Base64 image to the user's OneDrive and generates a public sharing link.
 * @param {string} imageData - The Base64 encoded image data (e.g., 'data:image/jpeg;base64,...').
 * @param {string} fileName - The desired file name (e.g., 'keyFobImage.jpg').
 * @returns {Promise<string|null>} A promise that resolves to the sharing URL or null if an error occurs.
 */
export async function uploadImageAndGetLink(imageData, fileName) {
  try {
    const graphClient = await createGraphClient();
    const driveItemPath = `/me/drive/special/approot:/uploaded-images/${fileName}:/content`;
    
    // Convert the Base64 data URL to a Blob more efficiently using fetch
    const fetchRes = await fetch(imageData);
    const blob = await fetchRes.blob();

    // Ensure the folder exists before uploading.
    // The .get() call will fail if the folder doesn't exist.
    try {
        await graphClient.api("/me/drive/special/approot:/uploaded-images").get();
    } catch (err) {
        // If the folder doesn't exist, create it.
        await graphClient.api("/me/drive/special/approot/children").post({
            name: "uploaded-images",
            folder: {}
        });
    }

    // Upload the file to OneDrive
    const uploadResponse = await graphClient.api(driveItemPath).put(blob);
    const driveItemId = uploadResponse.id;

    // Create a share link with 'anyone' scope
    const createLinkResponse = await graphClient.api(`/me/drive/items/${driveItemId}/createLink`).post({ type: 'view', scope: 'anonymous' });
    
    return createLinkResponse.link.webUrl;
  } catch (error) {
    console.error("Graph API error:", error);
    return null;
  }
}

// For debugging purposes, expose the Office object to the developer console.
if (typeof window !== 'undefined') {
  window.Office = Office;
  window.Client = Client;
}
