// outlook.js

import { templates } from "./templates.js";
import { uploadImageAndGetLink } from "./graph.js";

export async function pasteToOutlook(templateKey, selectedImage) {
  const template = templates[templateKey];
  if (!template) return;

  const values = {};
  template.variables.forEach(v => {
    values[v.name] = document.getElementById(v.name)?.value || `{{${v.name}}}`;
  });

  values.documentName = template.documentName;
  values.documentShortText = typeof template.documentShortText === "function" ? template.documentShortText(values) : template.documentShortText;
  
  const subject = template.subject(values);
  const item = Office.context.mailbox.item;
  
  // Check if in compose mode by checking for setSelectedDataAsync
  const isInComposeMode = (item.itemType === Office.MailboxEnums.ItemType.Message && typeof item.body.setSelectedDataAsync === "function");

  if (templateKey === 'keyFob' && selectedImage) {
    const fileName = `keyFobImage-${Date.now()}.jpg`;
    const imageUrl = await uploadImageAndGetLink(selectedImage, fileName);
    
    if (imageUrl) {
      // The image in the template will now be a link to OneDrive
      values.keyFobImage = imageUrl;
    } else {
      // Fallback if the upload fails
      values.keyFobImage = 'The key fob image could not be embedded. Please insert it manually or check your permissions.';
    }
    
    const emailBody = template.body(values);

    if (isInComposeMode) {
      item.body.setSelectedDataAsync(emailBody, { coercionType: Office.CoercionType.Html });
      item.subject.setSelectedDataAsync(subject, { coercionType: Office.CoercionType.Text });
    } else {
      item.displayReplyAllFormAsync({ htmlBody: emailBody, subject: subject });
    }
  } else {
    // For other templates or if no image is selected
    const emailBody = template.body(values);
    
    if (isInComposeMode) {
      item.body.setSelectedDataAsync(emailBody, { coercionType: Office.CoercionType.Html });
      item.subject.setSelectedDataAsync(subject, { coercionType: Office.CoercionType.Text });
    } else {
      item.displayReplyAllFormAsync({ htmlBody: emailBody, subject: subject });
    }
  }
}